.profile{
    padding: 70px;
    margin-left: 30px;
    margin-top: 30px;
    width: 30%;
    background-color: rgb(219, 215, 215);
}

.description img{
    border-radius: 100px;  
}

.description{
    display: flex;
    flex-direction: column;
    align-items: center;
    background: white;
    padding: 20px;
    border-radius: 5px;
    border-bottom: 1px solid  slategrey;;
}

.name, .tag, .location{
    margin-bottom: 0;
}

.name{
    font-weight: 600;
    font-size: 25px;
    color: rgb(61, 61, 61);
}

.tag, .location{
    color: rgb(134, 132, 132);
}

.stats{
    list-style: none;
    padding-left: 0;
    margin: 0;
    display: flex;
    justify-content: space-around;
    flex-direction: row;
    border-bottom: 1px solid  slategrey;
    border-left: 1px solid  slategrey;
    border-right: 1px solid  slategrey;
    border-bottom-left-radius: 5px;
    border-bottom-right-radius: 5px;
    background-color: rgb(243, 241, 241);

}

.stats li{
    display: flex;
    align-items: center;
    flex-direction: column;
    width: 33%;
    height: 60px;
}

.stats li:not(:last-child){
    border-right: 1px solid  slategrey;;
}


.label, .quantity{
    display: block;
    text-align: center;
}

.label{
    margin-bottom: 3px;
    margin-top: 10px;
    color: rgb(134, 132, 132);
    
}

.quantity{
    font-weight: 700;
}