.friendList {
    display: flex;
    justify-content: space-around;
    padding: 10px;
    /* width: 370px; */
    background: #ffffff;
    box-shadow: 0px 2px 1px rgba(0, 0, 0, 0.2), 0px 1px 1px rgba(0, 0, 0, 0.14),
    0px 1px 3px rgba(0, 0, 0, 0.12);
    border-radius: 0px 0px 4px 4px;
}

.item {
    display: flex;
    flex-direction: column;
    align-items: center;
}

